import React, { useEffect, useState } from 'react';
import { GoogleMap, LoadScript } from '@react-google-maps/api';
import Papa from 'papaparse';  // Import papaparse to parse CSV
import csvData from '../data/Crime2024-09-28.csv';  // Import the CSV file

const mapStyles = {
  height: "100vh",
  width: "100%"
};

const defaultCenter = {
  lat: 33.7490,  // Atlanta, GA
  lng: -84.3880
};

const MapComponent = () => {
  const [map, setMap] = useState(null);
  const [heatmap, setHeatmap] = useState(null);
  const [heatmapData, setHeatmapData] = useState([]);

  // Function to parse the CSV and set the heatmap data
  useEffect(() => {
    Papa.parse(csvData, {
      download: true,
      header: true,
      complete: (result) => {
        const parsedData = result.data.map(row => {
          // Convert each CSV row to a LatLng object, ensuring valid lat and long values
          const lat = parseFloat(row.lat);
          const lng = parseFloat(row.long);
          if (!isNaN(lat) && !isNaN(lng)) {
            return new window.google.maps.LatLng(lat, lng);
          }
          return null;
        }).filter(point => point !== null);  // Filter out invalid rows
        setHeatmapData(parsedData);
      }
    });
  }, []);

  useEffect(() => {
    if (map && window.google && window.google.maps && heatmapData.length > 0) {
      const heatmapLayer = new window.google.maps.visualization.HeatmapLayer({
        data: heatmapData,
        map: map
      });
      setHeatmap(heatmapLayer);
    }
  }, [map, heatmapData]);

  const toggleHeatmap = () => {
    if (heatmap) {
      heatmap.setMap(heatmap.getMap() ? null : map);
    }
  };

  const changeGradient = () => {
    if (heatmap) {
      const gradient = [
        "rgba(0, 255, 255, 0)",
        "rgba(0, 255, 255, 1)",
        "rgba(0, 191, 255, 1)",
        "rgba(0, 127, 255, 1)",
        "rgba(0, 63, 255, 1)",
        "rgba(0, 0, 255, 1)",
        "rgba(0, 0, 223, 1)",
        "rgba(0, 0, 191, 1)",
        "rgba(0, 0, 159, 1)",
        "rgba(0, 0, 127, 1)",
        "rgba(63, 0, 91, 1)",
        "rgba(127, 0, 63, 1)",
        "rgba(191, 0, 31, 1)",
        "rgba(255, 0, 0, 1)",
      ];
      heatmap.set("gradient", heatmap.get("gradient") ? null : gradient);
    }
  };

  const changeRadius = () => {
    if (heatmap) {
      heatmap.set("radius", heatmap.get("radius") ? null : 20);
    }
  };

  const changeOpacity = () => {
    if (heatmap) {
      heatmap.set("opacity", heatmap.get("opacity") ? null : 0.2);
    }
  };

  return (
    <div>
      <div style={{ textAlign: "center", padding: "10px" }}>
        <button onClick={toggleHeatmap}>Toggle Heatmap</button>
        <button onClick={changeGradient}>Change Gradient</button>
        <button onClick={changeRadius}>Change Radius</button>
        <button onClick={changeOpacity}>Change Opacity</button>
      </div>
      <LoadScript
        googleMapsApiKey={process.env.REACT_APP_GOOGLE_MAPS_API_KEY}
        libraries={["visualization"]}
      >
        <GoogleMap
          mapContainerStyle={mapStyles}
          center={defaultCenter}
          zoom={13}
          onLoad={(mapInstance) => setMap(mapInstance)}
        />
      </LoadScript>
    </div>
  );
};

export default MapComponent;
